import None from "@/components/qy/none.vue"
export default {
	components: {
		None
	},
	data() {
		return {
			btn_name: 'cou_t_rr',
			c_index: 0,
			list: [],
			class_name: 'cou_t_l_01',
			state: 0,
			list_empty: false,
			is_over:2
			
		};
	},
	onLoad() {
		this.get_all_coupon()
	},
	methods: {
		time_over(time) {
			if(!time){
				return false;
			}
			let a=new Date(time).getTime()
			let b=new Date().getTime()
			console.log(a)
			console.log(b)
			if(b>a){
				return true;
			} 
		},
		get_all_coupon() {
			this.list = this.$api.json_cms.get_coupon
		},
		del_coupon(id) {
			const that = this
			uni.showModal({
				title: '提示',
				content: '确认操作？',
				success: function(res) {
					if (res.confirm) {
						console.log('用户点击确定');
						that.$api.msg('操作成功')
						that.get_all_coupon()
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		num(index) {
			if (index == 0) {
				this.c_index = index
				this.class_name = "cou_t_l_01"
				this.state = 0
			}
			if (index == 1) {
				this.c_index = index
				this.class_name = "cou_t_l_01"
				this.state = 1
			}
			if (index == 2) {
				this.c_index = index
				this.class_name = "cou_sss"
				this.state = 2
			}
		},

	},
	onPullDownRefresh() {
		this.get_all_coupon()
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
